@font-face {
  font-family: "Averta Demo PE Cutted Demo";
  font-style: normal;
  font-weight: 400;
  src: url(/themes/myproject/fonts/averto/Averto.ttf);
}
.astudio__dp {
  display: block;
  margin: 0 14px 0 14px;
}
.astudio__dp h2 {
  font-family: "Averta Demo PE Cutted Demo";
  font-size: 40px;
  line-height: 49.44px;
  color: #000000;
  margin: 0;
}
.astudio__dp p {
  font-family: "Averta Demo PE Cutted Demo";
  font-size: 16px;
  line-height: 25px;
  color: #565656;
}
@media (min-width: 768px) and (max-width: 1024px) {
  .astudio__dp {
    display: grid;
    grid-template-columns: 440px 1fr;
    align-items: center;
  }
}
@media (min-width: 1025px) {
  .astudio__dp {
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    margin: 0 80 0 80;
  }
}
@media (min-width: 1440px) {
  .astudio__dp {
    margin: 0 120px 0 120px;
  }
}
.astudio__dp-block1-img img {
  height: 100%;
  width: 100%;
}
.astudio__dp-block1-videoIcon {
  background-image: url(".../../../../../images/Group 7.png");
  position: relative;
  width: 78px;
  height: 78px;
  left: 8rem;
  top: 10rem;
}
@media (min-width: 768px) and (max-width: 1024px) {
  .astudio__dp-block1-videoIcon {
    left: 11rem;
    top: 11rem;
  }
}
@media (min-width: 1025px) {
  .astudio__dp-block1-videoIcon {
    left: 16rem;
    top: 15rem;
  }
}
@media (min-width: 768px) and (max-width: 1024px) {
  .astudio__dp-block1-videoIcon {
    background-image: url(".../../../../../images/Group 7.png");
    position: relative;
    width: 78px;
    height: 78px;
    left: 8rem;
    top: 10rem;
    left: 18rem;
    top: 16rem;
  }
}
@media (min-width: 768px) and (max-width: 1024px) and (min-width: 768px) and (max-width: 1024px) {
  .astudio__dp-block1-videoIcon {
    left: 11rem;
    top: 11rem;
  }
}
@media (min-width: 768px) and (max-width: 1024px) and (min-width: 1025px) {
  .astudio__dp-block1-videoIcon {
    left: 16rem;
    top: 15rem;
  }
}
@media (min-width: 768px) and (max-width: 1024px) {
  .astudio__dp-block2 {
    margin-top: 34px;
  }
}
@media (min-width: 1025px) {
  .astudio__dp-block2 {
    margin-left: 41px;
    margin-top: auto;
    margin-bottom: auto;
  }
  .astudio__dp-block2-trimImg {
    position: absolute;
    left: 46rem;
    width: 178px;
    height: 178px;
    z-index: -5;
    background-image: url(".../../../../../images/Rectangle 26.png");
    margin-top: -34px;
  }
}
@media (min-width: 1025px) and (min-width: 1025px) {
  .astudio__dp-block2-trimImg {
    left: 41rem;
  }
}
@media (min-width: 1025px) and (min-width: 1440px) {
  .astudio__dp-block2-trimImg {
    left: 46rem;
  }
}

.roundImg {
  position: relative;
  z-index: -5;
  background: url(".../../../../../images/Ellipse 86.png");
}
@media (min-width: 1025px) {
  .roundImg {
    height: 129px;
    width: 129px;
    top: -8.6rem;
    left: 28rem;
  }
}
@media (min-width: 1440px) {
  .roundImg {
    height: 129px;
    width: 129px;
    top: -8.6rem;
    left: 29rem;
  }
}
@media (min-width: 768px) and (max-width: 1024px) {
  .roundImg {
    display: none;
  }
}